import { List, ListItem, Paper, TextField, Typography } from "@mui/material";
import { Container } from "@mui/system";
import { useState } from "react";
import { decompose } from "./cangjie";
import SearchIcon from "@mui/icons-material/Search";

function SearchCode() {
  const [sentence, setSentence] = useState();
  const [instructions, setInstructions] = useState();

  const handleChange = (e) => {
    setSentence(e.target.value);
    setInstructions(decompose(e.target.value));
  };

  return (
    <Container maxWidth="sm">
      <Paper variant="outlined" sx={{ p: 2, mt: 2 }}>
        <TextField
          label=""
          value={sentence}
          onChange={handleChange}
          fullWidth
          InputProps={{
            startAdornment: <SearchIcon color="disabled" sx={{ mr: 2 }} />,
          }}
        />
        <List>
          {instructions &&
            instructions.map(
              (instruction, index) =>
                instruction.codes !== undefined && (
                  <ListItem key={index} disablePadding>
                    <Typography>
                      {instruction.word}: {instruction.keys}
                    </Typography>
                  </ListItem>
                )
            )}
        </List>
      </Paper>
    </Container>
  );
}

export default SearchCode;
